<template>
	<div>
		<div class="out">
			<el-carousel :interval="4000" type="card">
				<el-carousel-item v-for="item in pics" >
					<img :src="item.imageUrl" class="pic" alt="">
				</el-carousel-item>
			</el-carousel>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				pics: [],
			}
		},
		methods: {
			/* 所有歌单分类 */
			allBanner() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/banner")
					.then((response) => {
						that.pics = response.data.banners;
					}, (err) => {})
			},
		},
		mounted: function() {
			this.allBanner();
		}
	}
</script>

<style scoped>
	.el-carousel__container {
		height: 350px;
	}
	
	.el-carousel__item h3 {
		color: #475669;
		font-size: 14px;
		opacity: 0.75;
		min-height: 350px;
		margin: 0;
	}
	.out{
		width: 100%;
		height: 350px;
		margin: 0 auto;
	}
	img{
		border-radius: 20px;
		width: 520px;
		margin-top: 30px;
		margin-left: 50px;
		box-shadow: 0 2px 4px rgba(0, 0, 0, .788), 0 0 6px rgba(0, 0, 0, .04);
	}
</style>
