<template>
	<!-- 热门推荐 -->
	<div class="hot">
		<!-- 热门推荐导航 -->
		<div class="hot-Introduce">
			<div class="hotTitle">
				<a>歌</a>
				<a>单</a>
				<a>推</a>
				<a>荐</a>
			</div>

			<div class="hot-Introduce-totle">
				<span v-for="item in listTags">
					<a class="hotTitle1" @click="findtoplayList(item.name)">{{item.name}}</a>
				</span>
			</div>
			<br>
			<a class="hot-more">
				<span class="more">————————————————————————————————</span>
				<span @click="jumpHot()" class="more1">更&nbsp;多</span>
				<span class="more">————————————————————————————————</span>
			</a>
		</div>
		<!-- 热门推荐内容 -->
		<div class="hot-main">
			<ul class="hot-main-content" v-for="item in toplayList">
				<div id="box" class="box">
					<img class="hot-main-pics" :src="item.coverImgUrl" alt=""  @click="findList(item.id)">
				</div>
				<p class="hot-main-list"  @click="findList(item.id)"> {{item.name}}</p>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hot',
		data() {
			return {
				/* 推荐歌单 */
				toplayList: [],
				/* 获得热门歌单类别 */
				listTags: []
			}
		},
		methods: {
			/* 热门推荐分类 */
			playlistTags() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/hot")
					.then((response) => {
						that.listTags = response.data.tags;
					}, (err) => {})
			},
			/* 发现热门推荐 */
			findtoplayList(name) {
				var that = this;
				console.log(name);
				this.$axios.get("https://autumnfish.cn/top/playlist/highquality?limit=10&cat="+name)
					.then((response) => {
						that.toplayList = response.data.playlists;
					}, (err) => {})
			},
			jumpHot(){
				this.$router.push("hotPage");
			},
			findList(id) {
				console.log(id);
				this.$store.commit("findId",id);
				this.$router.push("playListDetail");
			}
		},
		mounted: function() {
			this.findtoplayList("全部");
			this.playlistTags();
		}
	}
</script>

<style scoped>
	.hot{
		width: 100%;
	}
	.more1{
		padding: 5px;
	}
	.hotTitle {
		font-size: 40px;
		font-weight: 700;
		color: #393939;
		width: 100%;
		text-align: center;
		font-family: "微软雅黑";
		margin: 0 auto;
	}

	.hotTitle a {
		margin: 10px;
	}

	.hot-Introduce-totle {
		font-size: 20px;
		width: 100%;
		text-align: center;
		margin-top: 40px;
	}

	.hotTitle1 {
		color: #393939;
		margin-left: 30px;
	}

	.hot-more {
		width: 100%;
		font-size: 15px;
		color: #393939;
		margin-left: 90px;
	}

	.hot-more .more {
		color: #C20C0C;
		font-weight: 700;
	}

	.hot-main {
		/* margin: 0 auto; */
		margin-top: 30px;
		margin-left: 45px;
	}
	.hotTitle1:hover{
		cursor: pointer;
		color: #c20c20
	}
	.more:hover{
		cursor: pointer;
		color: #c20c20;
	}

	.hot-main-content {
		width: 15%;
		float: left;
	}

	.hot-main-pics {
		float: left;
		width: 200px;
		height: 200px;
		border-radius: 10%;
		border: 1px solid #d3d3d3;
		box-shadow: 0 2px 4px rgba(0, 0, 0, .788), 0 0 6px rgba(0, 0, 0, .04);
	}

	.hot-main-list {
		font-size: 17px;
	}

	#box{
		width: 210px;
		height: 210px;
		border: red;
		margin: 10% auto;
		overflow: hidden;
		border-radius: 10%;
	}
	img{
		transform: all 5s;
	}
	img:hover{
		transform: scale(2);
	}
	.more1:hover{
		color: #C20C20;
		cursor: pointer;
	}
	.hotTitle1:hover{
		color: #C20C0C;
	}
	.hot-main-list:hover{
		color: #C20C20;
		cursor: pointer;
	}
	.hot-main-pics:hover{
		color: #C20C20;
		cursor: pointer;
	}
</style>
