<template>
	<div class="Ranking-List">
		<div class="hot-Introduce">
			<div class="hotTitle">
				<a>排</a>
				<a>行</a>
				<a>榜</a>
			</div>
			<br>
			<a class="hot-more">
				<span class="more">————————————————————————————————</span>
				<span class="more1" @click="toRank">更&nbsp;多</span>
				<span class="more">————————————————————————————————</span>
			</a>
		</div>

		<div class="Ranking-List-main">
			<div class="Ranking-List1" v-for="p in 5">
				<ul class="Ranking-List1-main-content">
					<div id="box">
						<img class="Ranking-List1-logo" @click="toRank" :src="newRankings[p-1] && newRankings[p-1].coverImgUrl">
					</div>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 获得榜单内容 */
				newRankings: [],
			}
		},
		methods: {
			/* 新碟上架 */
			/* 首先建立一个循环，如果循环达到条件就停止查询数据，如果没有到达固定的数量就继续循环 */
			findnewRankings() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/toplist/detail")
					.then((response) => {
						that.newRankings = response.data.list;
					}, (err) => {})
			},
			toRank(){
				this.$router.push({
					name:"ranking"
				})
			}
		},
		mounted: function() {
			this.findnewRankings();
		}
	}
</script>

