<template>
	<div class="MV">
		<div class="hot-Introduce">
			<div class="hotTitle">
				<a>M</a>
				<a>V</a>
			</div>
			<br>
			<a class="hot-more">
				<span class="more">—————————————————————————————————</span>
				<span class="more">—————————————————————————————————</span>
			</a>
		</div>
		<div class="MV-main">
			<ul class="MV-main-content" v-for="item in newMVs">
				<div id="box" class="box">
					<img class="MV-main-pics" :src="item.cover" alt="">
					<a>
						<div class="mask">
					<h3><i class="el-icon-caret-right" @click="playMv(item.mv.id)"></i></h3>
						</div>
					</a>
				</div>
				<p class="MV-main-list"  @click="playMv(item.mv.id)">{{item.mv.title}}</p>
			</ul>
			<div class="onmv" v-show="isPlayMv">
				<i class="el-icon-close" @click="hide"></i>
				<video class="center-block" :src="mvUrl" controls="controls" autoplay="autoplay"></video>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 获得新碟上架 */
				newMVs: [],
				//MV地址
				mvUrl: "",
				isPlayMv: false
			}
		},
		methods: {
			/* 新碟上架 */
			findNewMV() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/top/mv?limit=10")
					.then((response) => {
						that.newMVs = response.data.data;
					}, (err) => {})
			},
			//获取视频资源
			playMv(mvid) {
				var that = this;
				this.$axios.get("https://autumnfish.cn/mv/url?id=" + mvid)
					.then((response) => {
						that.isPlayMv = true;
						that.mvUrl = response.data.data.url;
					}, (err) => {})
			},
			hide() {
				this.isPlayMv = false;
			}
		},
		mounted: function() {
			this.findNewMV();
		}
	}
</script>

