<template>
	<div class="VCD">
		<div class="hot-Introduce">
			<div class="hotTitle">
				<a>新</a>
				<a>碟</a>
				<a>上</a>
				<a>架</a>
			</div>
			<br>
			<a class="hot-more">
				<span class="more">————————————————————————————————</span>
				<span class="more1" @click="toAlbum">更&nbsp;多</span>
				<span class="more">————————————————————————————————</span>
			</a>
		</div>
		<div class="VCD-main">
			<ul class="VCD-main-content" v-for="item in newVCDs">
				<div id="box">
					<img class="VCD-main-pics" :src="item.artist.picUrl" alt="">
				</div>
				<p class="VCD-main-list">{{item.name}}</p>
				<p class="VCD-main-list">{{item.artist.name}}</p>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 获得新碟上架 */
				newVCDs: []
			}
		},
		methods: {
			/* 新碟上架 */
			findNewVCD() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/album/new?area=EA&limit=5")
					.then((response) => {
						that.newVCDs = response.data.albums;
					}, (err) => {})
			},
			toAlbum(){
				this.$router.push({
					name:"album",
				})
			},
		},
		mounted: function() {
			this.findNewVCD();
		}
	}
</script>

<style scoped>
	.more1:hover{
