		padding: 5px;
	}
	.hotTitle {
		font-size: 40px;
		font-weight: 700;
		color: #393939;
		width: 250px;
		font-family: "微软雅黑";
		margin: 0 auto;
	}
	.VCD-main-pics:hover{
		color: #C20C20;
